.about__container {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
                0 8px 16px -8px rgba(0, 0, 0, 0.3),
                0 -6px 15px -6px rgba(0, 0, 0, 0.025);
    padding: 20px;
    border-radius: 5px;
    background-color: honeydew;
    margin: 0 auto;
    width: 100%;
    max-width: 600px;
    font-weight: 300;
}

.about__container span:first-child {
    font-size: 20px;
}

.about__container span:last-child {
    display: block;
    margin-top: 10px;
}

.about__container h2, h4 {
    color: #2c2c2c;
}

.about__container div {
    color: dimgray;
}